.App {
  text-align: center;
}
.addd{
  border: 2px solid pink;
  background-color: rgb(151, 101, 109);
  height:65px;
  width:400px;
  margin: auto;
  text-align: center;
}
.add{

  border: 2px solid pink;
  background-color: rgb(165, 95, 107);
  height:300px;
  width:400px;
  margin: auto;
  text-align: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
